input.form__programm-input[type="checkbox"] + label::before {
  position: absolute;
  top: -1px;
  left: -6px;
  display: block;
  width: 30px;
  height: 30px;
  content: " ";
  border: 1px solid #cfcfcf;
  background-color: #ffffff; }

input.form__programm-input[type="checkbox"] + label:active {
  opacity: 0.3; }

input.form__programm-input[type="checkbox"] + label:hover::before {
  border: 1px solid #bcbcbc; }

input.form__programm-input[type="checkbox"]:checked + label::before {
  background: url("../img/icon-tick.svg") no-repeat;
  background-position: center;
  background-size: 22px; }

input.form__programm-input[type="checkbox"]:focus + label::before {
  outline: #3b99fc auto 5px;
  outline-offset: -2px; }

